import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ApiService } from 'src/app/servies/api/api.service';
import { CommonService } from 'src/app/servies/common/common.service';
import { Options } from "@angular-slider/ngx-slider";
import { CommonDialogService } from 'src/app/common-data/common-dialog.service';

@Component({
  selector: 'app-jobs',
  templateUrl: './jobs.component.html',
  styleUrls: ['./jobs.component.scss']
})
export class JobsComponent implements OnInit {
  options: Options = {
    floor: 0,
    ceil: 50
  };
  optionss: Options = {
    floor: 0,
    ceil: 99999
  };
  industry: any;
  industries:any = [];
  type: any;
  search: any = '';
  totalItems:any;
  jobs: any;
  currentPage: any = 1;
  fullTimes : boolean = false;
  partTime : boolean = false;
  contract : boolean = false;
  jobTypes : any = [];
  jobType : any = [
    {
      key : 'Full Time', val : 'FULL_TIME'
    },
    {
      key : 'Part Time', val : 'PART_TIME'
    },
    {
      key : 'Contract' , val : 'CONTRACT'
    }
  ];
  salary : any = {
    min : 0,
    max : 99999
  };
  experience : any = {
    exmin : 0,
    exmax : 50
  }

  industryCategory: any = [];

  constructor(
    private api : ApiService,
    private common : CommonService,
    private activateRoute : ActivatedRoute,
    private commonModal : CommonDialogService
  ) { }

  ngOnInit(): void {
    this.activateRoute.queryParams
    .subscribe(params => {
      this.type = params['type'];
      if(this.type == 1){
        this.search = params['search'];
      }
    })
    this.getJobs();
    this.getIndustry();
  }

  getIndustry(){
    this.api.getRequest("getindustry",'').subscribe((res : any)=>{
      this.industry = res.data;
    })
  }

  onIndustryChange(email: string, isChecked: boolean){
    if (isChecked) {
      this.industries.push(email);
    } else {
      let index = this.industries.findIndex(x => x.val == email)
      this.industries.splice(index,1);
    }
    this.getJobs();
  }
  getJobs(){     
    const body = {
      name : this.search,
      page : this.currentPage,
      size : 10,
      jobType : this.jobTypes,
      salary : this.salary,
      experience : this.experience,
      city : '',
      industry_category : this.industries
    }
    this.api.postRequest('getjob',body).subscribe((res : any)=>{
      if(res.statusCode == 200){
        this.jobs = res.data;
        this.totalItems = res.total;
      }else{
        this.common.errorMsg(res.message);
      }
    })
  }

  onChange(email: string, isChecked: boolean) {
    console.log(email, isChecked);
    
    if (isChecked) {
      this.jobTypes.push(email);
    } else {
      let index = this.jobTypes.findIndex(x => x.val == email)
      this.jobTypes.splice(index,1);
    }
    this.getJobs();
  }

  jobDetails(id,isApplied){
      this.commonModal.jobDetails(id).subscribe((res : any)=>{
        this.getJobs();
      })
  }

  onPageChange(pages) {
    this.currentPage = pages.page;
    this.getJobs();
  }
}