import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CommonDialogService } from 'src/app/common-data/common-dialog.service';
import { ApiService } from 'src/app/servies/api/api.service';
import { CommonService } from 'src/app/servies/common/common.service';

@Component({
  selector: 'app-education',
  templateUrl: './education.component.html',
  styleUrls: ['./education.component.scss']
})
export class EducationComponent implements OnInit {

  articles: any;
  currentPage: any =  1;
  totalItems:any;
  currentPage1: any =  1;
  totalItems1:any;
  // serialNumber: number = 0;
  search : any = '';
  limit: any = 12;
  educations: any;
  constructor(
    private api : ApiService,
    private common : CommonService,
    private commonDialog : CommonDialogService,
    private route : Router
  ) { }

  ngOnInit(): void {
    this.getData();
    this.getEducation();
  }
  getData(){
    this.api.getRequest(`getarticle?page=${this.currentPage}&size=${this.limit}&name=${this.search}`,'').subscribe((res : any)=>{
      if(res.statusCode == 200){
        this.articles = res.data;
        this.totalItems = res?.total;        
      }else{
        this.common.errorMsg(res.message);
      }
    }, (error: any) => {
      console.log(error);        
    })
  }

  getEducation(){
    this.api.getRequest(`geteducationvideo?page=${this.currentPage1}&size=${this.limit}&name=${this.search}`,'').subscribe((res : any)=>{
      if(res.statusCode == 200){
        this.educations = res.data;
        this.totalItems1 = res?.total;
      }else{
        this.common.errorMsg(res.message);
      }
    }, (error: any) => {
      console.log(error);        
    })
  }

  onPageChange(pages) {
    this.currentPage = pages.page;
    this.getData();
  }

  onPageChange1(pages) {
    this.currentPage1 = pages.page;
    this.getEducation();
  }

  addBookmark(val,saved){    
    if(localStorage.getItem('token')){
      this.api.postRequest("savedlist",{socialId : val , isSaved : !saved}).subscribe((res : any)=>{
        if(res.statusCode == 200){
          this.getData();
        }else{
          this.common.errorMsg(res.message);
        }
      })
    }else{
      this.commonDialog.showModal().subscribe((res : any)=>{
        this.getData();
      })
    }
  }

  openDetails(data){
    this.route.navigate(['/main/details'], { queryParams: { type: 3 }});
  }
  openDetailss(data){
    this.route.navigate(['/main/details'], { queryParams: { type: 4 }});
  }
}