import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CommonDialogService } from 'src/app/common-data/common-dialog.service';
import { ApiService } from 'src/app/servies/api/api.service';
import { CommonService } from 'src/app/servies/common/common.service';

@Component({
  selector: 'app-events',
  templateUrl: './events.component.html',
  styleUrls: ['./events.component.scss']
})
export class EventsComponent implements OnInit {

  events: any;
  currentPage: any =  1;
  totalItems:any;
  serialNumber: number = 0;
  search : any = '';
  limit: any = 12;

  constructor(
    private api : ApiService,
    private common : CommonService,
    private commonDialog : CommonDialogService,
    private route : Router
  ) { }

  ngOnInit(): void {
    this.getEvents();
  }

  getEvents(){
    this.api.getRequest(`getevents?page=${this.currentPage}&size=${this.limit}&name=${this.search}`,'').subscribe((res : any)=>{
      if(res.statusCode == 200){
        this.events = res.data;
        this.totalItems = res?.pageCount;
      }
    }, (error: any) => {
      console.log(error);        
    })
  }

  onPageChange(pages) {
    this.currentPage = pages.page;
    this.getEvents();
  }

  addBookmark(val){
    if(localStorage.getItem('token')){
      this.api.postRequest("savedlist",{socialId : val}).subscribe((res : any)=>{
        if(res.statusCode == 200){
          this.getEvents();
        }else{
          this.common.errorMsg(res.message);
        }
      })
    }else{
      this.commonDialog.showModal().subscribe((res : any)=>{
        this.getEvents();
      })
    }
  }

  openDetails(data){
    this.route.navigate(['/main/details'], { queryParams: { type: 1 }});
  }

}
